import * as React from 'react';
import { Product } from '../models/Product';
import Result from './Result';

type props = {
  products: Product[]
}

class state {
  activeFilter: string
  filterValues: string[]
}

class Accessories extends React.Component<props, state> {
  constructor(Props: props) {
    super(Props);
    this.state = {
      activeFilter: "All",
      filterValues: ["All", "Watches", "Sunnies", "Necklaces", "Earrings", "Rings"]
    };
  }

  onFilterClick(value: string) {
    $('.filter-' + this.state.activeFilter.replace("/", "-")).removeClass("active-filter"); // Must work with class names without using special characters (such as '/')
    $('.filter-' + value.replace("/", "-")).addClass("active-filter");
    this.state = {
      activeFilter: value,
      filterValues: this.state.filterValues
    }
    this.setState(this.state);
  }

  render() {
    return (<div>
      <br />
      <h2 className="text-center">Accessories</h2>
      <div className="container">
        <hr />
        <div className="row justify-content-center">
          {this.state.filterValues.map((value: string) => {
            return <div onClick={() => this.onFilterClick(value)} className={this.state.activeFilter == value ? "active-filter filter filter-" + value.replace("/", "-") : "filter filter-" + value.replace("/", "-")}>{value}</div>
          })}
        </div>
        <div className="row text-center text-lg-left">
          {this.props.products["products"].map((product: Product, id = 0) => {
            if (this.state.activeFilter != "All") {
              if (product.category == "accessories" && product.filter == this.state.activeFilter.toLowerCase().replace("/", "-")) return <Result {...product} key={id} />
              else return null
            } else {
              if (product.category == "accessories") return <Result {...product} key={id} />
              else return null
            }
          })}
        </div>
      </div>
    </div>
    );
  }
}

export default Accessories;




